@tailwind base;
@tailwind components;
@tailwind utilities;


@font-face{
  font-family:'Roboto';
  src:local('Roboto'),url(../public/fonts/RobotoMono-VariableFont_wght.ttf) format('truetype');
}

@font-face{
  font-family:'Poppins';
  src:local('Poppins'),url(../public/fonts/Poppins-Regular.ttf) format('truetype');
}

body {
  color:white;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Poppins;
  font-weight: 400;
  font-style: normal;
  background-color: black;
}

